.container {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-left: 20px;
  }
  
  .form {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 300px;
  }

  .register{
    margin-bottom: 15px;
  }

  .formInput {
    width: 250px;
    padding: 8px;
  }
  
  .formButton {
    background-color: #4caf50;
    color: white;
    padding: 10px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  
  }
  
  .formButton:hover {
    background-color: #45a049;
  }